<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="150px"
			>
			<el-form-item class="add-item" label="" prop="jiajumingcheng">
				<el-input v-model="ruleForm.jiajumingcheng" 
					placeholder="" clearable :disabled=" false  ||ro.jiajumingcheng"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajufenlei">
				<el-input v-model="ruleForm.jiajufenlei" 
					placeholder="" clearable :disabled=" false  ||ro.jiajufenlei"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajufengge">
				<el-input v-model="ruleForm.jiajufengge" 
					placeholder="" clearable :disabled=" false  ||ro.jiajufengge"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.jiajutupian)" prop="jiajutupian">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.jiajutupian?ruleForm.jiajutupian:''"
					@change="jiajutupianUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="jiajutupian">
				<img v-if="ruleForm.jiajutupian.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.jiajutupian.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.jiajutupian.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiajucailiao">
				<el-input v-model="ruleForm.jiajucailiao" 
					placeholder="" clearable :disabled=" false  ||ro.jiajucailiao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="pinpai">
				<el-input v-model="ruleForm.pinpai" 
					placeholder="" clearable :disabled=" false  ||ro.pinpai"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="guige">
				<el-input v-model="ruleForm.guige" 
					placeholder="" clearable :disabled=" false  ||ro.guige"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.baoxiushipin)" prop="baoxiushipin">
				<file-upload
					tip=""
					action="file/upload"
					:limit="1"
					:type="2"
					:multiple="true"
					:fileUrls="ruleForm.baoxiushipin?ruleForm.baoxiushipin:''"
					@change="baoxiushipinUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="baoxiushipin">
				<div class="viewBtn" v-if="ruleForm.baoxiushipin" @click="download(baseUrl + ruleForm.baoxiushipin)" type="success"></div>
				<div class="unviewBtn" v-else disabled></div>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuzhanghao">
				<el-input v-model="ruleForm.yonghuzhanghao" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="yonghuxingming">
				<el-input v-model="ruleForm.yonghuxingming" 
					placeholder="" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="lianxifangshi">
				<el-input v-model="ruleForm.lianxifangshi" 
					placeholder="" clearable :disabled=" false  ||ro.lianxifangshi"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="baoxiuyuanyin">
				<editor 
					v-model="ruleForm.baoxiuyuanyin" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					jiajumingcheng : false,
					jiajufenlei : false,
					jiajufengge : false,
					jiajutupian : false,
					jiajucailiao : false,
					pinpai : false,
					guige : false,
					baoxiuyuanyin : false,
					baoxiushipin : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
					lianxifangshi : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					jiajumingcheng: '',
					jiajufenlei: '',
					jiajufengge: '',
					jiajutupian: '',
					jiajucailiao: '',
					pinpai: '',
					guige: '',
					baoxiuyuanyin: '',
					baoxiushipin: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
					lianxifangshi: '',
				},


				rules: {
					jiajumingcheng: [
						{ required: true, message: '', trigger: 'blur' },
					],
					jiajufenlei: [
					],
					jiajufengge: [
						{ required: true, message: '', trigger: 'blur' },
					],
					jiajutupian: [
					],
					jiajucailiao: [
						{ required: true, message: '', trigger: 'blur' },
					],
					pinpai: [
					],
					guige: [
					],
					baoxiuyuanyin: [
					],
					baoxiushipin: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
					lianxifangshi: [
						{ validator: this.$validate.isMobile, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='jiajumingcheng'){
							this.ruleForm.jiajumingcheng = obj[o];
							this.ro.jiajumingcheng = true;
							continue;
						}
						if(o=='jiajufenlei'){
							this.ruleForm.jiajufenlei = obj[o];
							this.ro.jiajufenlei = true;
							continue;
						}
						if(o=='jiajufengge'){
							this.ruleForm.jiajufengge = obj[o];
							this.ro.jiajufengge = true;
							continue;
						}
						if(o=='jiajutupian'){
							this.ruleForm.jiajutupian = obj[o].split(",")[0];
							this.ro.jiajutupian = true;
							continue;
						}
						if(o=='jiajucailiao'){
							this.ruleForm.jiajucailiao = obj[o];
							this.ro.jiajucailiao = true;
							continue;
						}
						if(o=='pinpai'){
							this.ruleForm.pinpai = obj[o];
							this.ro.pinpai = true;
							continue;
						}
						if(o=='guige'){
							this.ruleForm.guige = obj[o];
							this.ro.guige = true;
							continue;
						}
						if(o=='baoxiuyuanyin'){
							this.ruleForm.baoxiuyuanyin = obj[o];
							this.ro.baoxiuyuanyin = true;
							continue;
						}
						if(o=='baoxiushipin'){
							this.ruleForm.baoxiushipin = obj[o];
							this.ro.baoxiushipin = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
						if(o=='lianxifangshi'){
							this.ruleForm.lianxifangshi = obj[o];
							this.ro.lianxifangshi = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}
				// 
				this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						var json = res.data.data;
						if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
							this.ro.yonghuzhanghao = true;
						}
						if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
							this.ruleForm.yonghuxingming = json.yonghuxingming;
							this.ro.yonghuxingming = true;
						}
						if((json.lianxifangshi!=''&&json.lianxifangshi) || json.lianxifangshi==0){
							this.ruleForm.lianxifangshi = json.lianxifangshi;
							this.ro.lianxifangshi = true;
						}
					}
				});

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`jiajubaoxiu/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`jiajubaoxiu/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			jiajutupianUploadChange(fileUrls) {
				this.ruleForm.jiajutupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			baoxiushipinUploadChange(fileUrls) {
				this.ruleForm.baoxiushipin = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

